      this.value2 = this.filter(d);
      // console.log("resDate", resDate);
      const start = new Date();
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 1);
      // const resDate1 = d.getFullYear() + '-' + this.p((d.getMonth() + 1)) + '-' + this.p(d.getDate())
