      start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
      // const resDate1 = d.getFullYear() + '-' + this.p((d.getMonth() + 1)) + '-' + this.p(d.getDate())
      // this.query(resDate,resDate1)
      // console.log("start", start);
      this.value1 = this.filter(start);
      // console.log("resdate", resDate1);
      // this.query(resDate, resDate1);
      this.http();
    },
    query(e, f) {
      if (this.timer) {
        const resDate1 = this.filter(f);
        console.log("resDate1", resDate1);
        this.status = this.custom.value11;
        this.area = this.custom.value2;
        this.street = this.custom.value3;
        this.restaurantcat = this.custom.value4;
        this.starttime = resDate1;
        this.lasttime = e;
        //  this.arr1 = [],
        //  this.arr2 = [],
        this.axios();
        this.timer = false;
      } else {
        if (this.value5 == undefined || this.value6 == undefined) {
          this.starttime = null;
          this.lasttime = null;
          this.area = this.custom.value2;
          this.status = this.custom.value11;
          this.street = this.custom.value3;
          this.restaurantcat = this.custom.value4;
          this.axios();
        } else {
          console.log("value5", this.value5);
          // const d = new Date(this.value5);
          const resDate = this.filter(this.value5);
          console.log("value5", resDate);
          const e = new Date(this.value6);
          const resDate1 = this.filter(this.value6);
          // e.getFullYear() +
          // "-" +
          // this.p(e.getMonth() + 1) +
          // "-" +
          // this.p(e.getDate());
          console.log(resDate1);

          this.area = this.custom.value2;
          this.status = this.custom.value11;
          this.street = this.custom.value3;
          this.restaurantcat = this.custom.value4;
          this.starttime = resDate;
          this.lasttime = resDate1;
          console.log("this.value6", this.street);
          this.axios();
        }

        //  this.arr1 = [],
        //  this.arr2 = [],
      }
