    filter(e) {
      const d = new Date(e);
      return (
        d.getFullYear() +
        "-" +
        this.p(d.getMonth() + 1) +
        "-" +
        this.p(d.getDate())
      );
    },
    p(s) {
      return s < 10 ? "0" + s : s;
    },
    http() {
      var _this = this;
      var restaurant = this.restaurant;
      var area = this.custom.value1;
      var street = this.custom.value2;
      var restaurantcat = this.normal.value1;
      var status1 = this.status1;
      this.starttime = this.value1;
      this.lasttime = this.value2;
      var starttime = this.starttime;
      var lasttime = this.lasttime;
      console.log(area);

      var url = "http://47.112.16.168:8800/warning/list";
      // ?username="+username+"password="+password
      this.$http
        .post(
          url,
          {
            restaurant,
            area,
            street,
            restaurantcat,
            status1,
            starttime,
            lasttime
          },
          { headers: { token: token } }
        )
        .then(res => {
          _this.list = res.data.data;
          _this.initStatus();
          _this.list8 = [];
          _this.initPageList();
          console.log(res, "res");
        })
        .catch(res => {
          console.log(res);
        });
