    // httpDetails(e){
    //     var _this = this;
    //   // var name = "谢氏烧烤摊";
    //   console.log(e)
    //   var url = "http://192.168.50.76:8800/warning/see?warningid="+e;
    //   // ?username="+username+"password="+password
    //   this.$http
    //     .get(
    //       url,
    //       // {
    //       //   name
    //       // },
    //       { headers: { token: "" } }
    //     )
    //     .then(res => {
    //       _this.list2 = res.data.data
    //      console.log(res)
    //     })
    //     .catch(res => {
    //       console.log(res);
    //     });
    // },
    start() {
      this.disabled = true;
    },
    end() {
      this.disabled = true;
    },
    option(value) {
      this.disabled1 = true;
      const start = new Date();
      console.log(value);
      //  console.log(value)
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      if (value == "一天") {
        this.value2 = year + "-" + month + "-" + day;
        // this.value1 = year+"-"+month+"-"+(day-1)

        start.setTime(start.getTime() - 3600 * 1000 * 24 * 1);
        this.value1 = this.filter(start);

        // console.log(1)
        console.log("start", this.value1);
        this.http();
      } else if (value == "三天") {
        this.value2 = year + "-" + month + "-" + day;
        start.setTime(start.getTime() - 3600 * 1000 * 24 * 3);
        this.value1 = this.filter(start);

        // console.log(1)
        console.log("start", this.value1);
        this.http();
      } else if (value == "一周") {
        this.value2 = year + "-" + month + "-" + day;
        start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
        this.value1 = this.filter(start);

        // console.log(1)
        console.log("start", this.value1);
        console.log(this.value1);
        this.http();
      } else if (value == "一个月") {
        this.value2 = year + "-" + month + "-" + day;
        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
        this.value1 = this.filter(start);

        // console.log(1)
        console.log("start", this.value1);
        this.http();
      } else if (value == "三个月") {
        this.value2 = year + "-" + month + "-" + day;
        start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
        this.value1 = this.filter(start);

        // console.log(1)
        console.log("start", this.value1);
        this.http();
      } else if (value == "一年") {
        this.value2 = year + "-" + month + "-" + day;
        start.setTime(start.getTime() - 3600 * 1000 * 24 * 365);
        this.value1 = this.filter(start);

        // console.log(1)
        console.log("start", this.value1);
        // console.log(this.starttime)
        this.http();
      }
    },
    initStatus() {
      var yistatus = this.list2.yistatus;
      var erstatus = this.list2.erstatus;
      var sanstatus = this.list2.sanstatus;
      if (yistatus == 2 || erstatus == 2 || sanstatus == 2) {
        // console.log(1)
        this.status = "已处理";
        this.status1 = 2;
        this.color = "text-shadow:5px 2px 6px #4caf50;";
        this.suibmit = false;
