      if (g == 2 && h == 0 && i == 0) {
        // console.log(1)
        this.status = "已处理";
        this.status1 = 2;
        console.log(1);
        // this.color ="text-shadow:5px 2px 6px #4caf50;"
        // this.suibmit = false
